import React from 'react'
import { Container, Grid, Image } from 'semantic-ui-react'
import Simple from '../../components/simple.js'
import img from '../../data/img/actual-oauth.jpg'
export default (props) => (
  <Simple
    title={'Our Tips'}
    description={
      <Container>
        “Sign in with Google/Facebook” buttons are a secure way to mitigate this problem.
        You only need to remember your passwords to these trusted services.
      </Container>
    }
    to='/recommendation/nooauth/'
    buttonText={'Next'}
  >
    <Grid.Row>
      <Grid.Column width={14}>
        <Image src={img} fluid />
      </Grid.Column>
    </Grid.Row>
  </Simple>
)



